"use client";

import { useEffect, useState } from "react";
import { MapContainer, TileLayer, Marker, Popup } from "react-leaflet";
import L from "leaflet";
import "leaflet/dist/leaflet.css";

// Create a custom red marker icon (no external PNG needed)
const createMarkerIcon = () => {
  return L.divIcon({
    html: `<div style="
      background-color: #ef4444;
      border: 2px solid white;
      border-radius: 50%;
      width: 24px;
      height: 24px;
      display: flex;
      align-items: center;
      justify-content: center;
      box-shadow: 0 2px 6px rgba(0,0,0,0.3);
      font-weight: bold;
      color: white;
      font-size: 12px;
    ">♥</div>`,
    className: "custom-marker",
    iconSize: [24, 24],
    iconAnchor: [12, 12],
    popupAnchor: [0, -12],
  });
};

export default function LeafletMap({ activeDonors }: any) {
  const [isClient, setIsClient] = useState(false);
  const idRef = useState(() => `leaflet-${Math.random().toString(36).slice(2)}`)[0];
  const mapRefKey = idRef; // key used to store map on window

  useEffect(() => {
    setIsClient(true);
  }, []);

  // Ensure any previously created map under this id is removed before creating a new one.
  useEffect(() => {
    if (typeof window === "undefined") return;
    (window as any).__leaflet_map_instances = (window as any).__leaflet_map_instances || {};
    const instances = (window as any).__leaflet_map_instances;
    if (instances[mapRefKey]) {
      try {
        instances[mapRefKey].remove();
      } catch (e) {
        // ignore cleanup errors
      }
      delete instances[mapRefKey];
    }

    return () => {
      if (instances[mapRefKey]) {
        try {
          instances[mapRefKey].remove();
        } catch (e) {}
        delete instances[mapRefKey];
      }
    };
  }, [mapRefKey]);

  const center =
    activeDonors?.length > 0
      ? ([activeDonors[0].location.latitude, activeDonors[0].location.longitude] as [number, number])
      : ([21.1466, 79.0889] as [number, number]);

  // Only render map on client side to prevent hydration mismatch
  if (!isClient) {
    return <div className="w-full h-full bg-gray-200" />;
  }

  return (
    <div className="w-full h-full relative">
      <MapContainer
        id={idRef}
        center={center}
        zoom={12}
        style={{ height: "100%", width: "100%", position: "absolute", top: 0, left: 0 }}
        whenCreated={(map) => {
          (window as any).__leaflet_map_instances = (window as any).__leaflet_map_instances || {};
          (window as any).__leaflet_map_instances[mapRefKey] = map;
        }}
      >
        <TileLayer url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png" />

        {(activeDonors || []).map((d: any, i: number) => (
          <Marker 
            key={i} 
            position={[d.location.latitude, d.location.longitude]}
            icon={createMarkerIcon()}
          >
            <Popup>
              <div>
                <b>{d.fullName}</b> <br />
                Blood: {d.bloodType}
              </div>
            </Popup>
          </Marker>
        ))}
      </MapContainer>
    </div>
  );
}
